$background: #282c34;
$background-transp: rgba(40, 44, 52, 0);
$offwhite: #cccccc;

.scroll-container {
  width: 100vw;
  z-index: 9;
  position: sticky;
  top: 25px;
  height: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.font-aws {
  color: $background;
  position: absolute;
  cursor: pointer;
  font-stretch: expanded;
}

.font-aws-bot {
  border: 5px solid $background;
  border-radius: 50%;
  padding: 5px 10px;
  transition: color 1s ease;
  transition: background-color 1s ease;
  transition: border 1s ease;
  font-size: 55px;
  left: calc(50vw - 42.5px);
  top: 80vh;
}

.scroll-text {
  font-weight: 900;
  position: absolute;
  top: 91vh;
  text-align: center;
}

@media (max-width: 550px) {
  .scroll-container {
    top: 40px;
    // display: none;
  }
}
