$background: #282c34;
$background2: rgb(71, 79, 94);
$background-transp: rgba(40, 44, 52, 0.8);
$offwhite: #cccccc;

span {
  display: flex;
  flex-direction: row;
}

* {
  user-select: none;
}

.navbar {
  z-index: 10;
  position: sticky;
  width: 100%;
  top: 0;
  background-color: $background;
  color: white;
  height: 40px;
  box-shadow: 0px 8px 5px rgba(30, 34, 42, 0.6);
}

.nav-name {
  display: flex;
  flex-direction: row;
  margin: 0;
  width: 500px;
}

.nav-text {
  background-color: $background;
  z-index: 10;
}

.numbers {
  font-size: 22px;
  margin: 2px 0 0 0;
}

.contact:hover {
  text-decoration: none !important;
}

.contact-me-menu {
  cursor: pointer;
  padding: 0 4px;
  display: flex;
  flex-direction: column;
}
.contact-me-menu-active {
  cursor: pointer;
  padding: 0 4px;
  background-color: $background2;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.menu-drop,
.menu-drop-close {
  position: absolute;
  background-color: $offwhite;
  top: 28px;
  right: 14px;
  width: 117px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border: 2px dotted $background;
  border-top: none;
}

.menu-mobile-sty {
  display: flex;
  flex-direction: column;
}

.empty-space {
  height: 5px;
  background-color: $background2;
}

.menu-mobile-sty > a {
  margin: 5px;
  padding: 2px 0;
  font-weight: 500;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.menu-mobile-sty > a:hover {
  background-color: $background;
  color: $offwhite;
}

.menu-mobile-sty > a:last-child {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.angle,
.m-icon {
  margin: auto 0;
}

.close,
.m {
  position: absolute;
}

.close {
  left: 188px;
  padding-right: 100px;
}

.hamburger-icon {
  // border: 2px solid $offwhite;
  // border-radius: 50%;
  // padding: 5px;
  // margin: 5px;
  display: none;
}

.nav-container {
  align-items: flex-end;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 15px;
}

.navbar-home {
  color: $offwhite;
  text-decoration: none;
}

.nav-link {
  padding-right: 5px;
  transition: 1s;
  text-decoration: none;
  color: white;
  font-size: 18px;
}

.nav-link:hover {
  text-decoration: underline;
}

.nav-link-container {
  display: flex;
  flex-direction: row;
}

.under {
  transition: 2s;
  width: 190px;
  border-bottom: 2px solid white;
}
