$background: #282c34;
$background-transp: rgba(49, 53, 62, 0.7);

@media (max-width: 1100px) {
  .about-me {
    // font-size: 1.7vw;
    width: 45%;
  }

  .about-container {
    padding: calc(50vh - (1.7vw * 7) + 40px) 0;
  }
}

@media (max-width: 1000px) {
  .m-icon {
    display: none;
  }
  .empty-space {
    height: 137px;
    width: 70%;
  }

  .menu-drop {
    width: 100%;
    top: 40px;
    right: 0;
    border: 0;
    border-radius: 0;
    border-bottom: 2px solid $background;
    animation-name: hamburger;
    animation-duration: 0.5s;
    overflow-y: hidden;
  }

  .menu-drop,
  .menu-drop-close {
    display: flex;
    flex-direction: row;
  }

  .menu-drop-close {
    // height: 130px;
    width: 100%;
    top: 40px;
    right: 0;
    border: 0;
    border-radius: 0;
    // animation-name: hamburger2;
    // animation-duration: 5s;
    // overflow-y: hidden;
  }
  @keyframes hamburger2 {
    0% {
      height: 130px;
    }
    60% {
      height: 100px;
    }
    80% {
      height: 80px;
    }
    100% {
      height: 1px;
    }
  }

  @keyframes hamburger {
    0% {
      height: 1px;
    }
    60% {
      height: 130px;
    }
    80% {
      height: 120px;
    }
    100% {
      height: 130px;
    }
  }

  .menu-drop > a {
    justify-content: space-around;
  }
  .hamburger-icon {
    display: block;
  }
  .angle {
    display: none;
  }
  .work-logo {
    margin: 25px 0;
  }
  .info-wrapper {
    display: none;
  }
  .nav-link-container {
    // display: none;
  }

  .nav-link {
    display: none;
  }
  .about-container {
    flex-direction: column;
  }

  .about-me {
    text-align: center;
    width: 80vw;
  }

  .about-tag {
    font-size: 3vw;
  }

  .about-tag-container {
    width: 90%;
    justify-content: space-around;
    display: flex;
    flex-direction: row;
  }

  .camera {
    font-size: 60px;
    margin: 0 0 35px 10px;
    padding: 0;
  }

  .project-pic {
    width: 75vw;
  }

  .project-header {
    font-size: 52px;
  }

  .svg {
    width: 50px;
  }

  .national-parks2 {
    margin-top: 73px;
    font-size: 30px;
    margin-left: 25px;
  }

  .project-desc-cont {
    width: 450px;
  }

  .links {
    display: none;
  }
}

@media (max-width: 800px) {
  .about {
    height: 70vh;
  }

  .about-container {
    padding: 40% 0 !important;
  }
  .bloggle2,
  .toDo2,
  .community2 {
    width: 75vw !important;
  }

  .project-container:nth-child(even) {
    background-color: $background !important;
  }

  .project-container:nth-child(odd) {
    background-color: $background-transp !important;
  }

  .toDo2 {
    font-size: 30px;
  }
  .about-me {
    // font-size: 14px;
  }

  .about-me {
    width: 95vw;
  }

  .about-container {
    height: 120px;
    padding: calc(50vh - 120px + 40px) 0;
  }

  .project-pic {
    // margin-top: 50px;
  }
  .project-pic-hover {
    display: none;
  }

  .project-container {
    height: 375px;
  }

  .project-container:last-child {
    margin-top: 25px;
    height: 325px;
  }

  .project-container:nth-child(2) {
    padding-top: 25px;
  }

  .project-desc-cont {
    width: 85vw;
  }

  .project-header {
    // width: 450px;
  }

  .quackle2,
  .community2 {
    margin: 0;
    padding: 0;
  }

  .quackle {
    height: 75px;
  }
  .svg {
    width: 40px;
  }

  .quackle2 {
    height: 20px;
    width: 135px;
    margin: 15px 0 0 125px !important;
    padding: 0 !important;
    padding-top: 20px !important;
    padding-left: 20px !important;
    line-height: 0.05;
    font-size: 35px;
    letter-spacing: 3.5px;
  }

  .search-icon {
    display: none;
  }

  .community2 {
    text-align: center;
    margin-top: 35px;
  }
}

@media (max-width: 550px) {
  .about-container {
    flex-direction: row;
  }

  .about-tag {
    font-size: 16px;
  }

  .about-tag-container {
    flex-wrap: wrap;
  }

  .footer-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .icon-wrapper {
    margin-top: 10px;
  }

  .back-to-top {
    display: none;
  }
}

